.topbar-wrapper {
	padding: 10px;

	.topbar {
		max-width: 1000px;
		margin: 0 auto;
		@include clear-float;

		.left {
			float: left;

			p {}
			color: gray;
			font-size: 32px; }

		.right {
			float: right;

			a {
				border-bottom: 3px solid transparent;
				padding: 11px;
				display: block;
				cursor: pointer;
				color: cornflowerblue;

				&:hover {
					border-bottom: 3px solid #ff9900;
					color: #006699; } } } } }

